import React from 'react'
import './About.css'
import SpeedRoundedIcon from '@material-ui/icons/SpeedRounded';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import DevicesIcon from '@material-ui/icons/Devices';
import WbIncandescentIcon from '@material-ui/icons/WbIncandescent';
import BubbleChartIcon from '@material-ui/icons/BubbleChart';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import avatar from '../images/avatar.jpg';
import LinearProgress from '@material-ui/core/LinearProgress';
import Box from '@material-ui/core/Box';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { render } from 'react-dom'
import { useInView } from 'react-intersection-observer'



function About() {
  
  const [progress, setProgress] = React.useState();
  const [ref, inView] = useInView({
    threshold: 0.3,
    triggerOnce: true
  })

  React.useEffect(() => {
    setProgress(0)

    const timer = setInterval(() => {
      
      setProgress((oldProgress) => {
        const diff = Math.random();
        return Math.min(oldProgress + diff, 98);
      });
    }, 1 );

    return () => {
      clearInterval(timer);
      
    };
  }, [inView]);

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: "20px",
    padding:"10px",
    width: "70%",
    borderRadius: 0,
    // marginLeft: "900px",
    // marginTop:"-500px"
  },
  colorPrimary: {
    backgroundColor: theme.palette.grey[theme.palette.type === 'light' ? 200 : 700],
  },
  bar: {
    borderRadius: 0,
    backgroundColor: '#65d6ce',

  },
}))(LinearProgress);
    return (
        <div ref={ref} >
             <h1>About Me</h1>

             {inView ? <div className="iconcombo">
                 <Card style={{boxShadow:"none"}} >
      <CardActionArea >
          <SpeedRoundedIcon id="icon" />
        
        <CardContent>
         <Typography id="question2" style={{margin:"0", padding:"0"}}  gutterBottom variant="h5" component="h2">
            Fast
          </Typography>

          <br />
          <Typography style={{margin:"0", padding:"0"}} id="description2" variant="body2" color="textSecondary" component="p">
          Fast load times and lag free interaction, my highest priority.
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
    <Card style={{boxShadow:"none"}} >
      <CardActionArea>
          <DevicesIcon id="icon" />
        
        <CardContent>
          <Typography id="question2" style={{margin:"0", padding:"0"}}  gutterBottom variant="h5" component="h2">
            Responsive
          </Typography>
          <br />

          <Typography id="description2" style={{margin:"0", padding:"0"}} variant="body2" color="textSecondary" component="p">
          My layouts will work on any device, big or small.
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
    <Card style={{boxShadow:"none"}} >
      <CardActionArea>
          <WbIncandescentIcon id="icon" />
        
        <CardContent>
          <Typography id="question2" style={{margin:"0", padding:"0"}}  gutterBottom variant="h5" component="h2">
          Intuitive
          </Typography>
          <br />

          <Typography id="description2" style={{margin:"0", padding:"0"}} variant="body2" color="textSecondary" component="p">
          Strong preference for easy to use, intuitive UX/UI.          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
    <Card style={{boxShadow:"none"}}  >
      <CardActionArea>
          <BubbleChartIcon id="icon" />

        <CardContent>
          <Typography id="question2"  style={{margin:"0", padding:"0"}} gutterBottom variant="h5" component="h2">
          Dynamic
          </Typography>
          <br />

          <Typography id="description2" style={{margin:"0", padding:"0"}} variant="body2" color="textSecondary" component="p">
          Websites don't have to be static, I love making pages come to life.
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
            </div> : <div style={{height:"100%", width:"100%"}}> </div> }

           <div className="upperinfo">
           {inView ? <div className="info" ><Avatar id="avatar" alt="Puneet Kathar" src={avatar} />
            <br />
            <Typography id="question"  style={{margin:"0", padding:"0"}} gutterBottom variant="h5" component="h2">
          Who's this guy?
          </Typography>
          <br />
            <Typography  id="description" style={{margin:"0", padding:"0"}} variant="body2" color="textSecondary" component="p">
            I'm a Student of Jabalpur Engineering College, Madhya Pradesh, India. I have serious interest in Web Development and other programming skills. <br />
            Let's make something special.
          </Typography></div> : <div style={{height:"100%", width:"100%"}}> </div>}
          
         {inView ? <div className="allbars">
         <div   className="bar" > 
         <label className="bar_label" for="file" >Node.JS</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress - 1 } /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" >React.JS</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress} /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" >Next.JS</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress + 1} /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" >HTML/CSS</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress - 2} /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" >API's</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress} /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" >MongoDB</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress - 1} /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" >JS</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress - 3} /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" >D3.JS</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress - 4} /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" >GCP</label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress - 1} /></div>
         <div className="bar"  > 
         <label className="bar_label" for="file" > Other </label>
         <BorderLinearProgress className="bar2"  id="file" variant="determinate" value={progress - 2} /></div>
         </div> : null}
         </div>
        

        </div>
    )
}

export default About
